<template>
  <auth-layout>

    <div class="background"></div>

    <div class="auth-form">

      <div class="material-icons-outlined auth-icon">mark_email_unread</div>
      <h2>Підтвердження пошти</h2>
      <p>На ваш email було відправлено лист з посиланням. Перейдіть за посиланням з листа, аби завершити активацію</p>
      <div v-if="!showRetry">
        <button class='button-plain' @click="$router.push('/')">На головну</button>
        <small @click="showRetry = true">Лист не надійшов? <span class="link2">Надіслати повторно</span></small>
      </div>
      <div v-else>
        <input-email v-model='email' ref='email'/>
        <button type='submit' class='button-plain' @click="confirm()">Надіслати повторно</button>
      </div>

    </div>

  </auth-layout>
</template>

<script>
import controller from '@/api/controller'
import authLayout from '@/layouts/auth-layout.vue'
import inputEmail from '@/components/input-email.vue'
export default {
  components: { authLayout, inputEmail },
  data() { return {
    showRetry: false,
    email: '',
  }},
  methods:  {
    async confirm() {
      this.$refs.email.v$.$touch()
      if (!this.$refs.email.v$.$error) {
        await controller.resendActivationLink(this.email)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.background {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(#0008, #0008), url('../assets/reg.png');
  background-position: left 20% top 50%;
  background-size: cover;
}
.auth-form {
  @media (min-width: 960px) {
    position: absolute;
    left: 25em;
    width: 22em;
    padding: 5em 2em;
  }
}
.link2 {
  text-decoration: underline;
}
</style>